/* =========================
   Carousel (Projects)
   ========================= */

.carousel-section {
  max-width: 1050px;
  margin: 0 auto;
  padding: 56px 16px 28px;
}

.section-title {
  letter-spacing: 5px;
  text-shadow: 2px -2px 6px rgba(77, 65, 9, 1);
  text-align: center;
  margin: 0 0 26px;
}

/* IMPORTANT: prevent arrow "jump" by stabilizing layout height */
.carousel-wrapper {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;

  /* Stable height so top:50% doesn't shift between slides */
  min-height: 560px;
  padding: 10px 0;
}

/* Card */
.carousel-card {
  width: 100%;
  max-width: 920px;
  border-radius: 22px;
  overflow: hidden;

  background: rgba(255, 255, 255, 0.70);
  border: 1px solid rgba(0, 0, 0, 0.08);

  box-shadow:
    0 18px 50px rgba(0, 0, 0, 0.18),
    0 2px 10px rgba(0, 0, 0, 0.08);

  transform: translateZ(0);
}

/* Image area */
.carousel-image {
  height: 320px;
  background: rgba(0, 0, 0, 0.03);
}

.carousel-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
  filter: saturate(1.05) contrast(1.02);
}

/* Text area */
.carousel-content {
  padding: 18px 20px 22px;
  font-family: "Poppins", sans-serif;

  /* Keep arrows stable even if copy length differs */
  min-height: 200px;
}

.carousel-content h3 {
  margin: 2px 0 10px;
  font-size: clamp(22px, 2.1vw, 34px);
  line-height: 1.15;
  font-weight: 650;
  color: rgba(20, 20, 20, 0.92);
  letter-spacing: 0.2px;
}

.carousel-content .desc {
  margin: 0 0 10px;
  font-size: clamp(14px, 1.1vw, 16px);
  line-height: 1.55;
  color: rgba(20, 20, 20, 0.78);
  max-width: 78ch;
}

.carousel-content .sub {
  margin: 0 0 14px;
  font-size: 12.5px;
  line-height: 1.4;
  color: rgba(20, 20, 20, 0.55);
  letter-spacing: 0.4px;
  text-transform: uppercase;
}

/* GitHub button */
.github-btn {
  display: inline-flex;
  align-items: center;
  gap: 8px;

  margin-top: 6px;
  padding: 10px 14px;
  border-radius: 14px;

  border: 1px solid rgba(80, 60, 255, 0.25);
  background: rgba(120, 100, 255, 0.12);

  color: rgba(60, 40, 210, 0.95);
  text-decoration: none;
  font-weight: 650;

  transition:
    transform 160ms ease,
    background 160ms ease,
    border-color 160ms ease,
    box-shadow 160ms ease,
    color 160ms ease;
}

.github-btn:hover {
  background: rgba(120, 100, 255, 0.20);
  border-color: rgba(80, 60, 255, 0.45);
  box-shadow: 0 10px 24px rgba(80, 60, 255, 0.18);
  transform: translateY(-1px);
}

.github-btn:active {
  transform: translateY(0px) scale(0.99);
}

.github-btn:focus-visible {
  outline: 3px solid rgba(80, 60, 255, 0.35);
  outline-offset: 2px;
}

/* Nav buttons */
.nav-btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 46px;
  height: 46px;
  border-radius: 999px;

  background: rgba(255, 255, 255, 0.85);
  border: 1px solid rgba(0, 0, 0, 0.10);

  box-shadow:
    0 12px 26px rgba(0, 0, 0, 0.14),
    0 2px 8px rgba(0, 0, 0, 0.06);

  cursor: pointer;
  user-select: none;

  transition: transform 160ms ease, background 160ms ease, box-shadow 160ms ease;
}

.nav-btn:hover {
  background: rgba(255, 255, 255, 0.95);
  box-shadow: 0 16px 34px rgba(0, 0, 0, 0.16);
  transform: translateY(-50%) scale(1.04);
}

.nav-btn:active {
  transform: translateY(-50%) scale(0.98);
}

.nav-btn.left { left: -6px; }
.nav-btn.right { right: -6px; }

/* Dots */
.dots {
  display: flex;
  justify-content: center;
  margin-top: 14px;
  gap: 8px;
}

.dot {
  width: 10px;
  height: 10px;
  border-radius: 999px;
  border: 1px solid rgba(0, 0, 0, 0.12);
  background: rgba(0, 0, 0, 0.18);
  cursor: pointer;
  transition: width 180ms ease, background 180ms ease, transform 180ms ease;
}

.dot:hover {
  transform: translateY(-1px);
  background: rgba(0, 0, 0, 0.28);
}

.dot.active {
  width: 26px;
  background: rgba(80, 60, 255, 0.55);
  border-color: rgba(80, 60, 255, 0.25);
}

/* Mobile */
@media (max-width: 768px) {
  .carousel-wrapper {
    min-height: 520px;
  }

  .carousel-image {
    height: 240px;
  }

  .nav-btn.left { left: 6px; }
  .nav-btn.right { right: 6px; }
}

/* Reduce motion */
@media (prefers-reduced-motion: reduce) {
  .github-btn,
  .nav-btn,
  .dot {
    transition: none;
  }
}
