/* ---------------- Fonts ---------------- */

@import url("https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400..700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap");

/* ---------------- CSS Variables ---------------- */

:root {
  --primary-color: #e6e6e6;
  --secondary-color: #383819;
  --text-color: #7a7a7a;
  --accent-color: #c8c8c8;

  --background-dark: #534949;
  --background-light: #cfcfcf;
}

/* ---------------- Global Reset ---------------- */

* {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  width: 100%;
  overflow-x: hidden;

  font-family: "Poppins", sans-serif;
  background: white;
  color: var(--text-color);

  cursor: url("./components/media/neu-cursor.png"), auto;
}

a {
  cursor: url("./components/media/neu-cursor-clicked.png"), auto;
}

/* ---------------- App Root ---------------- */

.App {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

/* ---------------- Global Typography ---------------- */

h2 {
  font-size: clamp(48px, 4.5vw, 84px);
  text-align: center;
  color: #ffffff;
  margin-bottom: 20px;
}

h3 {
  font-size: clamp(32px, 3.5vw, 64px);
  text-align: center;
  font-weight: 300;
  color: #c8c8c8;
  font-family: Georgia, "Times New Roman", Times, serif;
  margin: 0;
}

h4 {
  font-size: clamp(18px, 1.2vw, 28px);
  text-align: center;
  color: #ababab;
  margin: 6px 0;
}



@keyframes pulseBorder {
  0%, 100% { border-color: rgba(255, 255, 255, 0.2); }
  50%      { border-color: rgba(255, 255, 255, 0.3); }
}










@keyframes rainbowFlow {
  0% {
    background-position: 0% 100%;
  }
  100% {
    background-position: 100% 100%;
  }
}




@media (max-width: 768px) {

  .topbar {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 12px;             
    padding: 5px 5px;
    width: 80vw;
    height: auto;
    flex-wrap: wrap;     
    overflow-x: hidden;      
  }

  .topbar a {
    padding: 10px;
    font-size: 20px;
    white-space: nowrap;
  }

  .info-photo-container {
    width: 100vw;
    height: 900px;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    display: flex;
    margin-top: 5vh; 
    padding: 10px 10px;
    gap: 20px;       
    box-sizing: border-box;
    overflow-x: hidden; 
  }


  .myInfo {
    padding: 0 10px;
    font-size: clamp(20px, 4vw, 28px); 
    width: 100%;
    max-width: 90vw;
    line-height: 1.5;
    
    text-align: center;
  }

  .about {
    font-size: 28px; 
  }


  .myPic {
    width: 100%;
    overflow: hidden;  
    flex: none;  
    display: flex;
    justify-content: center;  /* horizontally center */
    align-items: center;
    height: auto;
    padding: 0;
    margin: 0;
    overflow-x: hidden;     
  }

  .myPic img {
    width: 70vw;
    max-width: 100%;    
    object-fit: contain;
    display: block;
    margin: 0 auto;
  }

  .portfolio-grid {
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    gap: 1rem;
    padding: 0.5rem;
  }

  .portfolio-card {
    width: 100%;
    aspect-ratio: 1 / 1;
  }

  .portfolio-card img {
    border-radius: 20px;
  }

  .portfolio-card .text {
  font-size: 0.9rem; 
  }

  .lets-connect a {
    margin: 20px 10px;
    padding: 10px;
    font-size: 3vw; /* responsive font */
    word-break: break-word; /* breaks long email */
    text-align: center;
  }

}


