.background {
  position: fixed;
  inset: 0;
  z-index: -1;
  background: white;
  overflow: hidden;
}

.background::before,
.background::after {
  content: "";
  position: absolute;
  width: 1000px;
  height: 1000px;
  filter: blur(220px);
  opacity: 0.5;
  background-repeat: no-repeat;
}

.background::before {
  top: 5%;
  left: 5%;
  background: radial-gradient(circle, #f50008, transparent 70%);
  animation: float 10s ease-in-out infinite;
}

.background::after {
  top: 40%;
  left: 70%;
  background: radial-gradient(circle, #e0dd03, transparent 70%);
  animation: floatReverse 6s ease-in-out infinite;
}

/* ---------------- Animations ---------------- */

@keyframes float {
  0%, 100% {
    transform: translate(0, 0);
  }
  50% {
    transform: translate(80vw, -60vh);
  }
}

@keyframes floatReverse {
  0%, 100% {
    transform: translate(0, 0);
  }
  50% {
    transform: translate(-40vw, -40vh);
  }
}
